export default {
  name: "SortItem",
  props: {
    sortText: {
      type: String,
      default: "",
    },
    /* 
      0:未选中
      1:desc
      2:asc
    */
    sortType: {
      type: Number,
      default: 0,
    },
  },
  data() {
    return {};
  },
  methods: {
    onSort() {
      this.$emit("onSort");
    },
  },
};
